import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"

const IndexPage = () => (
  <Layout>
    <SEO title="Home" />
    <article>
        <img className={"person_photo"} src={'../../soldatenkova/soldatenkova_1.png'} alt={""}/>
        <div>
          <div className={"person_name"}>Александра Солдатенкова</div>
        </div>
      <div className={"textBlock"}>
      <p>Сидеть дома мне очень сложно. Мне не&nbsp;нравится, то, что нельзя гулять с&nbsp;друзьями. Зато нравится, что я&nbsp;могу чуть дольше поспать.</p>
      <p>За&nbsp;это время я&nbsp;научилась делать выпечку. Хотела научиться вообще, готовить, но&nbsp;не&nbsp;хватает времени. <br />
      Я&nbsp;посмотрела фильм &laquo;Гарри Потер&raquo;, мне понравилось, и&nbsp;я&nbsp;бы рекомендовала его посмотреть всем. Он&nbsp;учит много чему хорошему.<br />
      <br />
      Вместе с&nbsp;моей семьей мы&nbsp;стали вместе играть в&nbsp;настольные игры. К&nbsp;сожалению, пришлось расстаться с&nbsp;совместными прогулками по&nbsp;лесу с&nbsp;нашими собаками.<br />
      Я&nbsp;общаюсь с&nbsp;друзьями через Воцап, потому что можно подключиться нескольким людям одновременно. Правда, происходили забавные случаи. Первый раз мы&nbsp;пытались позвонить одновременно друг другу, ничего не&nbsp;вышло. В&nbsp;другой раз у&nbsp;меня был онлайн урок, а&nbsp;подруги, не&nbsp;знав этого, пытались дозвониться мне. Я&nbsp;сбрасывала звонки, а&nbsp;девочки подумали, что я&nbsp;на&nbsp;что-то обиделась.</p>
      <p>А&nbsp;вот самое обидное, что празднование моего дня рождения пришлось отложить, потому что ни&nbsp;друзьям, ни&nbsp;родным нельзя было приехать к&nbsp;нам или в&nbsp;развлекательный центр и&nbsp;кинотеатр. Я&nbsp;грустила и&nbsp;немного поплакала. В&nbsp;первый день окончания карантина я&nbsp;отпраздную, пригласив друзей и&nbsp;близких, свой день рождения.</p>
      <p>Наши школьные учителя, как обычно, нестрогие и&nbsp;интересно объясняют уроки. Ученики&nbsp;же стали более развязанными и&nbsp;неуправляемыми, могу есть во&nbsp;время урока, выкрикивать, перебивая учителя.</p>
      <p>Мама говорит, что камера меня &laquo;любит&raquo;, но&nbsp;я, как любая девочка-подросток, не&nbsp;всегда довольна своей внешностью.</p>
      <p>После окончания пандемии все выйдут с&nbsp;чистыми руками, но&nbsp;без денег. Люди, скорее всего, ничего не&nbsp;переосмыслят, они редко учитывают опыт. Людям свойственно всё быстро забывать и&nbsp;возвращаться к&nbsp;своим привычкам.</p>
      <p>Я&nbsp;очень хочу посетить Японию!<br />
      Я&nbsp;бы выбрала&nbsp;бы суперспособность&nbsp;&mdash; излечивать людей от&nbsp;всех болезней, это&nbsp;бы помогло вылечить много смертельно больных...</p>      </div>
    </article>
  </Layout>
)

export default IndexPage



